.custom-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  overflow: hidden;
  outline: 0; }
  .custom-modal.custom-modal-open {
    background-color: rgba(0, 0, 0, 0.6);
    overflow-x: hidden;
    overflow-y: auto;
    display: block; }
  .custom-modal .modal-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100% - (1.75rem * 2));
    max-width: 700px;
    margin: 1.75rem auto;
    position: relative;
    width: auto;
    pointer-events: none; }
  .custom-modal .modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 95%;
    pointer-events: auto;
    background-color: var(--white);
    background-clip: padding-box;
    border: 1px solid rgba(179, 179, 179, 0.2);
    border-radius: 5px;
    outline: 0; }
    .custom-modal .modal-content.show {
      animation: fadeIn .3s ease-in-out 1; }
    .custom-modal .modal-content.hide {
      animation: fadeOut .3s ease-in-out 1; }

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(150px); }
  to {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fadeOut {
  from {
    transform: translateY(0);
    opacity: 1; }
  to {
    opacity: 0;
    transform: translateY(150px); } }
  .custom-modal .modal-close-container {
    text-align: right;
    padding: 10px; }
  .custom-modal .modal-close {
    display: inline-block;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    font-weight: bold;
    padding: 7px;
    cursor: pointer; }
  .custom-modal .modal-header {
    padding: 20px; }
  .custom-modal .modal-header h6 {
    margin-bottom: 0; }
  .custom-modal .modal-body {
    padding: 0 20px 20px 20px; }
  .custom-modal .modal-footer {
    padding: 0 20px 20px 20px; }

